<template>
    <div style="width:90%;">
        <div class="mb-6">
            <h2>Planes de entrenamiento</h2>
        </div>
        <div>
            <v-form >
                <v-row            
                v-for="(plan, index) in listadoPlanesSemanales"
                no-gutters
                class="mb-2"
                :key="plan.idPlanSemanal">
                    <v-btn style="width:82%" @click="push(plan.idPlanSemanal)">    
                        {{ plan.nombrePlanSemanal }}
                    </v-btn>
                    <v-btn style="width:4%" color="red" @click="eliminarPlan(plan.idPlanSemanal)">
                        <v-icon>mdi-delete</v-icon>
                    </v-btn>
                </v-row>
            </v-form>
            <!-- <v-form
            v-for="(plan, index) in listadoPlanesSemanales"
            no-gutters
            class="mb-2"
            :key="plan.idPlanSemanal">
                <v-card-title @click="push(plan.idPlanSemanal)">
                    {{ plan.nombrePlanSemanal }}
                </v-card-title>
                <v-spacer></v-spacer>
                    <v-icon color="red" @click="eliminarPlan(plan.idPlanSemanal)"
                    >mdi-delete</v-icon>
            </v-form> -->
        </div>
        <div class="d-flex justify-end mt-6">
            <v-btn  @click="dialog=true">
                <v-icon>mdi-plus</v-icon>
                <span>AÃ±adir Nuevo</span>
            </v-btn>
        </div> 
        <v-dialog 
        v-model="dialog">
            <v-card class="pa-4">
                <v-form class="pa-4">
                    <v-row>
                        <v-text-field
                        label="Nombre del plan"
                        v-model="nuevoPlanSemanal.nombre">
                        </v-text-field>
                    </v-row>
                    <v-row>
                        <v-text-field
                        label="Descripcion del plan"
                        v-model="nuevoPlanSemanal.descripcion">
                        </v-text-field>
                    </v-row>
                </v-form>
                <div>
                    <v-row class="pa-4">
                        <v-btn @click="cerrarDialog" >
                            <v-icon>mdi-close-box</v-icon>
                            Cerrar
                        </v-btn>
                        <v-spacer></v-spacer>
                        <v-btn @click="accionGuardar" >
                            <v-icon>mdi-content-save-edit</v-icon>
                            Guardar
                        </v-btn>
                    </v-row>
                </div>
            </v-card>
        </v-dialog>
    </div>
</template>

<script>
import utiles from '../utiles/funciones'
export default {

    data() {
        return{
            dialog: false,
            nuevoPlanSemanal:{
                idPlanSemanal: 0,
                nombre: '',
                descripcion: '',
                planDiario:[]
            }
        }
    },
    methods:{
        accionGuardar(){
            this.$store.commit('guardarPlanSemanalNuevo', {
                idPlanSemanal:this.nuevoPlanSemanal.idPlanSemanal,
                nombrePlanSemanal:this.nuevoPlanSemanal.nombre,
                descripcionPlanSemanal:this.nuevoPlanSemanal.descripcion,
                planDiario:this.nuevoPlanSemanal.planDiario
                })
            this.cerrarDialog()

        },
        eliminarPlan(idPlanSemanal){
            this.$store.commit('eliminarPlanSemanal', idPlanSemanal)
        },
        cerrarDialog(){
            this.dialog = false
            this.nuevoPlanSemanal.nombrePlanSemanal = '',
            this.nuevoPlanSemanal.descripcionPlanSemanal = ''
        },
        push(id){
            return this.$router.push(`${this.$router.currentRoute._rawValue.path}/${id}`)
        }

    },
    computed:{
        listadoPlanesSemanales(){
            return this.$store.getters.listadoPlanesSemanales
        }
    }
}

</script>

